import React, { useState } from "react";
import "../assets/style.css";
import Image1 from "../assets/Image/proaxiom-symbol+text.png";

import ButtonBold from "./Button-bold";
const Nav = () => {
  const [id, setId] = useState(1);
  const [ids, setIds] = useState(1);
  const handleChange = (a) => {
    setId(a);
  };
  const togglepage = () => {
    if (ids == 1) setIds(2);
    else setIds(1);
  };

  return (
    <>
      <div id="header-navigation" className=" nav-a">
        <div data-testid="header-nav-dropdown" className="nav-b">
          <div>
            <div className=" top-a">
              <div className=" top-text">
                <a onClick={() => handleChange(2)}>
                  <div tabindex="0" className=" top-content">
                    <svg
                      alt="Globe"
                      aria-hidden="true"
                      className="top-c"
                      width="48"
                      height="48"
                      viewBox="0 0 48 48"
                      fill="none"
                    >
                      <path
                        d="M24 44C35.0457 44 44 35.0457 44 24C44 12.9543 35.0457 4 24 4C12.9543 4 4 12.9543 4 24C4 35.0457 12.9543 44 24 44Z"
                        stroke="currentColor"
                        stroke-width="3"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      ></path>
                      <path
                        d="M4 24H44"
                        stroke="currentColor"
                        stroke-width="3"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      ></path>
                      <path
                        d="M24 4C29.0026 9.47671 31.8455 16.5841 32 24C31.8455 31.4159 29.0026 38.5233 24 44C18.9974 38.5233 16.1545 31.4159 16 24C16.1545 16.5841 18.9974 9.47671 24 4V4Z"
                        stroke="currentColor"
                        stroke-width="3"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      ></path>
                    </svg>
                    <span>English</span>
                    <svg
                      className="upper-arrow"
                      viewBox="0 0 13 7"
                      fill="none"
                      color="black"
                      version="1.1"
                      // xmlns="http://www.w3.org/2000/svg"
                      // xmlns:xlink="http://www.w3.org/1999/xlink"
                    >
                      <path
                        fill-rule="evenodd"
                        clip-rule="evenodd"
                        d="M5.8434 0.256281C6.20603 -0.0854279 6.79397 -0.0854278 7.1566 0.256281L12.728 5.50628C13.0907 5.84799 13.0907 6.40201 12.728 6.74372C12.3654 7.08543 11.7775 7.08543 11.4148 6.74372L6.5 2.11244L1.58517 6.74372C1.22254 7.08543 0.634602 7.08543 0.271972 6.74372C-0.0906581 6.40201 -0.0906571 5.84799 0.271973 5.50628L5.8434 0.256281Z"
                        fill="currentColor"
                      ></path>
                    </svg>
                  </div>
                </a>
                <div className="top-b">
                  <svg
                    alt="Globe"
                    tabindex="0"
                    className=" top-c"
                    width="48"
                    height="48"
                    viewBox="0 0 48 48"
                    fill="none"
                  >
                    <path
                      d="M24 44C35.0457 44 44 35.0457 44 24C44 12.9543 35.0457 4 24 4C12.9543 4 4 12.9543 4 24C4 35.0457 12.9543 44 24 44Z"
                      stroke="currentColor"
                      stroke-width="3"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    ></path>
                    <path
                      d="M4 24H44"
                      stroke="currentColor"
                      stroke-width="3"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    ></path>
                    <path
                      d="M24 4C29.0026 9.47671 31.8455 16.5841 32 24C31.8455 31.4159 29.0026 38.5233 24 44C18.9974 38.5233 16.1545 31.4159 16 24C16.1545 16.5841 18.9974 9.47671 24 4V4Z"
                      stroke="currentColor"
                      stroke-width="3"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    ></path>
                  </svg>
                </div>
                <div
                  className={`${
                    id == 2 ? "select-languageopen" : "select-languageclose"
                  }`}
                >
                  <div
                    data-focus-guard="true"
                    tabindex="0"
                    style={{
                      width: "1px",
                      height: "0px",
                      padding: "0px",
                      overflow: "hidden",
                      position: "fixed",
                      top: "1px",
                      left: "1px",
                    }}
                  ></div>
                  <div data-focus-lock-disabled="false">
                    <span
                      tabindex="0"
                      onClick={() => handleChange(1)}
                      className="language-active"
                    >
                      English
                      <svg
                        width="20"
                        height="21"
                        viewBox="0 0 20 21"
                        fill="none"
                      >
                        <path
                          d="M16.6666 5.5L7.49992 14.6667L3.33325 10.5"
                          stroke="#007EFF"
                          stroke-width="1.66667"
                          stroke-linecap="round"
                          stroke-linejoin="round"
                        ></path>
                      </svg>
                    </span>
                    <span
                      onClick={() => handleChange(1)}
                      tabindex="0"
                      className="language-notactive "
                    >
                      Français
                    </span>
                    <span
                      onClick={() => handleChange(1)}
                      tabindex="0"
                      className="language-notactive "
                    >
                      Español
                    </span>
                    <span
                      onClick={() => handleChange(1)}
                      tabindex="0"
                      className="language-notactive "
                    >
                      Deutsch
                    </span>
                    <span
                      onClick={() => handleChange(1)}
                      tabindex="0"
                      className="language-notactive "
                    >
                      Português
                    </span>
                  </div>
                  <div
                    data-focus-guard="true"
                    tabindex="0"
                    style={{
                      width: "1px",
                      height: "0px",
                      padding: "0p",
                      overflow: "hidden",
                      position: "fixed",
                      top: "1px",
                      left: "1px",
                    }}
                  ></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <nav className="sc-1n3rarh-5 header" data-testid="header">
          <div className="header-aj header-b">
            <a
              type="base"
              className="name header-name"
              href="/"
            >
              <div
                className="logo-display logo-wrapper"
                data-testid="logo"
                width="auto"
              >
                <img
                  src={Image1}
                  alt="Calendly logo"
                  width="auto"
                  className="logo-img"
                  style={{ width: "168px", height: "20px" }}
                />
                <img
                  src="data:image/svg+xml;base64,"
                  height="40px"
                  alt="Calendly logo"
                  className="logo-small"
                />
              </div>
            </a>
            <ul width="auto" className="nav-item">
              <li className="">
                <a
                  href="/for-individuals"
                  rel=""
                  target=""
                  id="header-menu-item-0"
                  type="base"
                  className=" nav-tab"
                >
                  Individuals
                </a>
              </li>
              <li className="">
                <a
                  aria-current="page"
                  href="/for-teams"
                  rel=""
                  target=""
                  id="header-menu-item-1"
                  type="base"
                  className="nav-tab"
                >
                  Teams
                </a>
              </li>
              <li className="">
                <a
                  href="/for-enterprise"
                  rel=""
                  target=""
                  id="header-menu-item-2"
                  type="base"
                  className="nav-tab"
                >
                  Enterprise
                </a>
              </li>
              <li className="arrow">
                <a
                  href="/features"
                  rel=""
                  target=""
                  id="header-menu-item-3"
                  type="base"
                  className="nav-tab"
                >
                  Product
                  <button
                    aria-expanded="false"
                    aria-label="Show"
                    aria-labelledby="header-menu-item-3"
                    aria-controls="Product"
                    className=" down-arrow button-a"
                  ></button>
                </a>
              </li>
              <li className="">
                <a
                  href="/pricing"
                  rel=""
                  target=""
                  id="header-menu-item-4"
                  type="base"
                  className="nav-tab"
                >
                  Pricing
                </a>
              </li>
              <li className="arrow">
                <a
                  href="/resources"
                  rel=""
                  target=""
                  id="header-menu-item-5"
                  type="base"
                  className="nav-tab"
                >
                  Resources
                  <button
                    aria-expanded="false"
                    aria-label="Show"
                    aria-labelledby="header-menu-item-5"
                    aria-controls="Resources"
                    className=" down-arrow button-a   "
                  ></button>
                </a>
              </li>
            </ul>
            <ul width="auto" id="right-side-components" className="nav-right">
              <li>
                <a
                  type="header"
                  href="/login"
                  className="nav-c"
                >
                  <span className="nav-right-l">Log In</span>
                  <img
                    src="data:image/svg+xml;base64,"
                    alt="User Profile Log In Button"
                    className="profile-icon"
                  />
                </a>
              </li>
              <li className="skip">
                <div className="button-right">
                  <ButtonBold
                    minheight="36px"
                    padding="7px 14px"
                    name="Get Started"
                    fontSize="0.875rem"
                  />
                  {/* <a
                    tabindex="0"
                    data-testid="primary-button"
                    href="/app/signup?lang=en"
                    font-weight="semiBold"
                    display="inline-flex"
                    opacity="1"
                    className="sc-dofdei-0 kmQQza"
                  >
                    <span className="sc-1u8fykh-0 exSulj">
                      <span font-size="s16" className="sc-1i1v580-0 bpJfnc">
                        Get started
                      </span>
                    </span>
                  </a> */}
                </div>
              </li>
              <li className="hamburger" onClick={() => togglepage()}>
                <div
                  className={` responsive-nav ${ids == 2 ? "active" : ""} `}
                  data-testid="hamburger-menu"
                >
                  <div className=" responsive-nav-a cross-1"></div>
                  <div className="responsive-nav-b cross-2"></div>
                  <div className="responsive-nav-c cross-3 "></div>
                </div>
              </li>
            </ul>
          </div>
          <ul
            className={`${ids == 2 ? "responsive-ul-open" : "responsive-ul"}`}
          >
            <li>
              <a
                href="/for-individuals"
                rel=""
                target=""
                type="base"
                className="header-name"
              >
                Individuals
              </a>
            </li>
            <li>
              <a
                aria-current="page"
                href="/for-teams"
                rel=""
                target=""
                type="base"
                className="header-name"
              >
                Teams
              </a>
            </li>
            <li>
              <a
                href="/for-enterprise"
                rel=""
                target=""
                type="base"
                className="header-name"
              >
                Enterprise
              </a>
            </li>
            <li className="arrow">
              <a
                href="/features"
                rel=""
                target=""
                type="base"
                className="header-name"
              >
                Product
              </a>
            </li>
            <li>
              <a
                href="/pricing"
                rel=""
                target=""
                type="base"
                className="header-name"
              >
                Pricing
              </a>
            </li>
            <li className="arrow">
              <a
                href="/resources"
                rel=""
                target=""
                type="base"
                className="header-name"
              >
                Resources
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </>
  );
};
export default Nav;
